import server from '../../server.js';
import uniLoadMore from '@/components/uni-load-more.vue';
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			msgNumber:[],
			MsgList:[],
			status: 'loading',
			systemCount:[],
			statusTypes: [{
				value: 'more',
				text: '加载前',
				checked: true
			}, {
				value: 'loading',
				text: '加载中',
				checked: false
			}, {
				value: 'noMore',
				text: '我是有底线的',
				checked: false
			}],
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			},
			auid:0
		}
	},
	onShow:function(){
		this.getNoReadMsgNumber();
		this.getMsgList();
		this.getNosystemNumber();
	},
	onLoad:function(){
		//this.getNoReadMsgNumber();
		this.auid=server.userinfo.Auid
	},
	methods: {
		getsysChat:function(){
			uni.navigateTo({
				url: 'systemMsg'
			});
		},
		getChat:function(id,id2){
			let go=parseInt(id)+parseInt(id2)
			uni.navigateTo({
				url: 'chat?id='+go
			});
		},
		getPage:function(e){
			if(e==1){
				uni.navigateTo({
					url: 'zan'
				})
			}
			if(e==2){
				uni.navigateTo({
					url: 'mpl'
				})
			}
			if(e==3){
				uni.navigateTo({
					url: 'newFans'
				})
			}
		},
		getNosystemNumber:function(){
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=getSystemMsg", //仅为示例，并非真实接口地址。
				data: {
					
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						this.systemCount=res.data.data
					}
					
					
					console.log(this.systemCount)
				}
			});
		},
		getNoReadMsgNumber:function(){
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=getMyNoRead", //仅为示例，并非真实接口地址。
				data: {
					
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						this.msgNumber=res.data.data
					}
					
					
					console.log(this.msgNumber)
				},
				complete() {
					uni.hideLoading();
				}
			});
		},
		getMsgList:function(){
			this.status ="more";
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=getMyMsgList", //仅为示例，并非真实接口地址。
				data: {
					
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						this.MsgList=res.data.data
					}
					if(res.data.code=="0" ||res.data.data.length<50 ){
						this.status ="noMore";
					}
					console.log(this.MsgList)
				},
				complete() {
					uni.hideLoading();
				}
			});
		}
	}
}
